.page__container {
  margin: 0 auto;
  max-width: 1200px;
  min-width: 320px;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  overflow: hidden;
}

.page__main {
  flex-grow: 1;
}

.page__body {
  min-height: 100%;
  margin: 0;
  background-color: $white;
}
